import React from 'react';

import { H2, H2SubHeading } from '../components/Typography/Typography';
import SkillsPageSkillList from '../components/SkillsPageSkillList/SkillsPageSkillList';
import Head from '../components/Head/Head';

import classes from './skills.module.scss';

const skills = () => {
  return (
    <div className={classes.SkillsPageBody}>
      <Head content="Skills of Satadru Roy">skills | Satadru Roy</Head>
      <div className={classes.SkillsPageHeadings}>
        <H2>my skills</H2>
        <H2SubHeading>
          <span className={classes.HighlightedText}>tools</span>,{' '}
          <span className={classes.HighlightedText}>languages</span> and{' '}
          <span className={classes.HighlightedText}>technologies</span> I use to
          build things
        </H2SubHeading>
      </div>
      <SkillsPageSkillList />
    </div>
  );
};

export default skills;
