import React from 'react';
import { motion } from 'framer-motion';

import Card from '../UI/Card/Card';

import classes from './SkillsPageSkillList.module.scss';

const MotionCard = motion.custom(Card);

const cardContainerVariant = {
  start: {
    y: '-0.5rem',
    scale: 1.01,
    originX: 0.5,
    originY: 1,
    backgroundColor: 'rgba(238, 241, 228, 1)',
    perspective: '100rem',
    backfaceVisibility: 'hidden',
    transform: 'translate3d(0, 0, 0)',
    transformStyle: 'preserve-3d',
    boxShadow: '0 1rem 3rem rgba(212, 236, 229, 0.3)',
  },
  end: {
    y: '0.5rem',
    scale: 1,
    boxShadow: '0 1rem 2rem rgba(212, 236, 229, 0.3)',
    backgroundColor: 'rgba(238, 241, 228, 0.85)',
    transition: {
      type: 'tween',
      duration: 1.8,
      ease: 'easeInOut',
      repeat: Infinity,
      repeatType: 'reverse',
    },
  },
};

const reqSvgs = require.context('../../assets/icons/skills', false, /\.svg$/);

const svgs = reqSvgs.keys().map(path => {
  const svgName = path.split('.')[2].toUpperCase();

  return (
    <div key={path} className={classes.SkillsPageSkillFlexItem}>
      <div key={path} className={classes.SkillsPageSkillSvgContainer}>
        {reqSvgs(path)(reqSvgs(path).defaultProps)}
      </div>
      <div className={classes.SkillsPageSkillText}>{svgName}</div>
    </div>
  );
});

const skillsPageSkillList = () => {
  return (
    <MotionCard
      variants={cardContainerVariant}
      initial="start"
      animate="end"
      // transition={{ type: 'spring', repeat: Infinity, damping: 12 }}
    >
      <div className={classes.SkillsPageSkillSvgList}>{svgs}</div>
    </MotionCard>
  );
};

export default skillsPageSkillList;
